}
.slide-show{
	position: relative;
	margin:15px 15px 15px 0;
	width: 900px;
	height: 500px;
	overflow: hidden;
}
.slide-show h2{
	position: absolute;
	width: 100%;
	height: 100%;
	color: #fff;
	background-color: #D7C5C1;
	opacity: .5s; /*????????????*/
	bottom: 0;
	height: 30px;
	text-align: left;
	padding-left: 15px;
	opacity: 0.4;
}
.slide-img{
	width: 100%;
}
.slide-img img{
	width: 100%;
